body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
}
.navbar li {
  display: inline-block;
  text-align: center;
}

.itemLeft {
  width: 15%;
}

.itemCenter {
	width: 70%;
}

.itemRight {
  width: 15%;
}

.navbar {
  background: blue;
  height: 100px;
}

li {
  color: white;
  padding: 15px 0;
}

body {
  font-size: 24px;
}

.header{
	background-color: gray;
	width: 100%;
	height: 300px;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
}

h2{
	text-align: center;
	font-size: 25px;
	padding-bottom: 40px;
}

.card {
    width: 300px;
    height: 150px;
    margin: 15px 45px;
    border: 3px solid white;
  }
  
.footer{
	background-color: palevioletred;
	width: 100%;
	height:80px;
	position: relative;

}

.footer p{
	color: white;
	text-align: left;
	padding: 10px 0;
	font-size: 30px;
}
.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 25px 0 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
}

